    <div>
        <Row type="flex" justify="start">
            <h1>XServer组件一览</h1>
        </Row>
        <Row>
            <Table :columns="columns1" :data="data1"></Table>
        </Row>
        <Row>
            <Back-top :height="100">返回顶部</Back-top>
        </Row>
    </div>
</template>
<script>
export default {
        data () {
            return {
                columns1: [
                    {
                        title: '类别',
                        align: 'center',
                        key: 'type'
                    },
                    {
                        title: '名称',
                        align: 'center',
                        key: 'name'
                    },
                    {
                        title: '版本',
                        align: 'center',
                        key: 'version'
                    },
                    {
                        title: '描述',
                        align: 'center',
                        key: 'descript'
                    },
                    {
                        title: '操作',
                        key: 'operate',
                        align: 'center',
                        render (row, column, index) {
                            return `<i-button type="primary" size="small" @click="show(${index})">使用方式</i-button>`;
