                    {
                        type: 'Express',
                        name: 'x-model',
                        version: '0.1.0',
                        descript: '极简风格的RESTful无后端中间件，只需写实体类，然后直接RESTful请求，全自动CRUD'
                    },
                    {
                        type: 'Express',
                        name: 'x-batis',
                        version: '0.1.0',
                        descript: '极简风格的RESTful无后端中间件，只需写SQL，然后直接RESTful请求，全自动CRUD'
                    },
                    {
                        type: 'Express',
                        name: 'x-nosql',
                        version: '0.1.0',
                        descript: 'NoSql服务应用路由中间件，快速构建轻量小巧灵活的NoSql服务'
                    },
                    {
                        type: 'Express',
                        name: 'x-auth',
                        version: '-',
                        descript: '构建中...'
                    }
                ]
            }
        }
