<template>
    <div>
        <Row type="flex" justify="start">
            <h1>开发预览</h1>
        </Row>
        <Row type="flex" justify="center">
            <Col span="24">
            <Timeline>
                <Timeline-item>
                    <p class="time">Step 1：基于标准RESTful请求</p>
                    <Card>
                        <p class="content">Example：http://localhost:8080/xmodel/user_model/create</p>
                        <p class="content">{ "username":"cheney", "password":"123" }</p>
                    </Card>
                </Timeline-item>
                <Timeline-item>
                    <p class="time">Step 2：使用标准应用中间件组件x-model处理请求和响应</p>
                    <Card>
                        <p class="content">var xmodel = require('x-model')</p>
                        <p class="content">app.use('/xmodel/', xmodel)</p>
                    </Card>
                </Timeline-item>
                <Timeline-item>
                    <p class="time">Step 3：定义实体类，{project}/src/model/UserModel.js</p>
                    <Card>
                        <p class="content">var UserModel = sequelize.define('test_user_model', { username: Sequelize.STRING, password: Sequelize.STRING })</p>
                    </Card>
                </Timeline-item>
                <Timeline-item color="green">
                    <Icon type="trophy" slot="dot"></Icon>
                    <Card>
                        <p class="time">Summary：总结</p>
                        <p class="content">只需三步，就完成了实体对象插入数据库，几乎无须编写任何业务逻辑代码，而这一切是因为<b>x-model</b>应用中间件已经提供基础的<b>CRUD</b>处理，url请求中的<b>user_model</b>会自动映射<b>UserModel,js</b>文件</p>
                    </Card>
                </Timeline-item>
                <Timeline-item><a href="#">查看更多应用服务中间件</a></Timeline-item>
            </Timeline>
            </Col>
        </Row>
        <Row>
            <Back-top :height="100">返回顶部</Back-top>
        </Row>
    </div>
</template>
<script>
export default {

}
</script>
<style scoped>
.time {
    font-size: 16px;
    font-weight: bold;
}

.content {
    font-family: "Helvetica Neue";
    font-size: 14px;
    padding-left: 5px;
    color: #657180;
}
</style>



